.sbb__hero {
    background: linear-gradient(111.25deg, rgba(0, 36, 69, 0.5) 2.75%, #002445 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
    url('../../assets/background.png');
    height: 90vh;
    max-height: 100vh;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center 60%;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 5%;
}

.sbb__hero-logo {
    padding-bottom: 5%;
}

.sbb__hero-text-h1 {
    font-family: "Heading" !important;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    color: white;
    font-size: 62px;
	text-align: left;
    
	padding-left: 5%;
}

.sbb__hero-text-h3 {
    font-family: "Paragraph" !important;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;

    padding-left: 5%;

    color: white;
    font-size: 45px;
    text-align: left;
}
