.sbb__about {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0px;
    gap: 10px;
    
    background: #002445;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;

    margin: 10% 20%;
}

.sbb__about-img {
    border-radius: 10px 0px 0px 10px;
}

.sbb__about-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 5% 5%;
    gap: 50px;
}

.sbb__about-header {
    font-family: 'Heading';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 34px;
    text-align: left;

    color: #FFFFFF;
}

.sbb__about-paragraph {
    font-family: 'Paragraph';
    text-align: left;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;

    color: #FFFFFF;
}

.sbb__about-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 22px 50px 15px;
    gap: 10px;

    background: #FFFFFF;
    border-radius: 100px;

    font-family: 'Heading';
    font-style: normal;
    font-weight: 400;
    font-size: 27px;
    line-height: 27px;

    color: #002445;
}