.sbb__form {
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    align-items: flex-start;
    padding: 20px;
    gap: 10px;

    margin: 10% 20%;

    border-radius: 10px;
}

.sbb__form-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
}

.sbb__form-text h1 {
    font-family: 'Heading';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 40px;

    color: #002445;
}

.sbb__form-text p {
    text-align: left;
    font-family: 'Paragraph';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;

    width: 500px;

    color: #002445;
}

.sbb__form-form {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 0px 20px;
    gap: 25px;
}

.sbb__form form input[type=text], .sbb__form textarea {
    all:unset;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 15px 25px;
    gap: 10px;
    background: #F1F1F1;
    border-radius: 15px;

    text-align: left;
    font-family: 'Paragraph';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    /* identical to box height */

    width: 500px;

    color: rgba(0, 0, 0, 0.5);
}

.sbb__form form input[type=submit] {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 22px 50px 15px;
    gap: 10px;

    background: #002445;
    border-radius: 100px;

    font-family: 'Heading';
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    color: #FFFFFF;

}